.container{
    padding: 20px 0;
    flex-grow: 1;
    color: #fff;
}

.title{
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 25px;
    line-height: 32px;
    font-family: 'Source Sans Pro';
    color: #fff;
}

.subtitle{
    color: #fff;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    margin-bottom: 30px;
    font-family: 'Source Sans Pro';
}

.items{
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 40px;
}

.item{
    width: 150px;
    padding: 10px;
    background: #1B1B1B;
    border-radius: 10px;
    transition: all .3s;
    color: #fff;
    cursor: pointer;
    font-family: 'Inter';
    font-weight: 500;
    font-size: 17px;
    line-height: 22px;
    text-transform: uppercase;
    text-align: center;

    &:hover{
        background: #0C6515;
    }
}

.table{
    margin-bottom: 80px;
    &Top, &Item{
        display: flex;
        align-items: center;
        padding: 20px;
        background: #413E3E;
        border-radius: 10px 10px 0px 0px;
        gap: 10px;

        span{
            color: #fff;
            width: 100%;
            font-family: 'Source Sans Pro';
        }


    }

    &Top + .tableItem{
        border-radius: 0px 0px 10px 10px;
    }

    &Item{
        border-radius: 10px;
        margin-bottom: 20px;
        padding: 40px 20px;
        background: #1B1B1B;
    }
}

@media(max-width: 850px){
    .table{
        padding: 0;
        margin-bottom: 20px;
        overflow-x: scroll;

        & > div{
            min-width: 850px;
        }
    }

    .items{
        margin-bottom: 30px;
        flex-wrap: wrap;
    }
}