.back{
    display: flex;
    margin-bottom: 20px;
    align-items: center;
    cursor: pointer;
    font-size: 17px;
    line-height: 22px;
    outline: none;
    border: none;
    background: transparent;
    color: #fff;

    svg{
        margin-right: 10px;
    }
}

.container{
    flex-grow: 1;
    margin-bottom: 70px;
}

.title{
    color: #fff;
    font-weight: 600;
    font-size: 28px;
    margin-bottom: 60px;
}

.content{
    display: flex;
    gap: 30px;
    align-items: stretch;

    & > div{
        width: 100%;
    }
}

.table{
    margin-bottom: 25px;
    width: 100%;

    &Header, &Row{
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: #413E3E;
        border-radius: 10px 10px 0px 0px;
        padding: 20px;

        span{
            font-size: 17px;
            line-height: 22px;
            width: 100%;
            color: #fff;
        }
    }

    &Row{
        background: #121212;
        border-radius: 0px 0px 10px 10px;
    }

}

.chat{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding-bottom: 10px;

    &Header{
        background: #413E3E;
        border-radius: 10px 10px 0px 0px;
        padding: 10px 20px;
        display: flex;
        align-items: center;
    }

    img{
        width: 44px;
        height: 44px;
        border-radius: 5px;
        margin-right: 10px;
    }

    &Name{
        font-size: 14px;
        color: #fff;
        margin-bottom: 4px;
    }

    &Status{
        font-size: 14px;
        color: #069514;
    }
}

.inputs{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 50px;

    & > div{
        width: 100%;
    }
}

.label{
    font-size: 14px;
    margin-bottom: 10px;
    color: #A6A6A6;
    display: block;
}

.input{
    border: none;
    outline: none;
    background: #1B1B1B;
    border-radius: 10px;
    padding: 20px;
    font-size: 17px;
    color: #fff;
}

.buyContent{
    display: flex;
    align-items: stretch;
    gap: 20px;
    position: relative;
    margin-bottom: 50px;
}

.select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    background: #1B1B1B;
    border-radius: 10px;
    padding: 20px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;

    &List{
        position: absolute;
        top: calc(100% + 10px);
        width: 50%;

        li{
            background: #1B1B1B;
            border-bottom: 1px solid #514E4E;
            padding: 5px 10px;
            color: #fff;
            cursor: pointer;
            transition: color .3s;
            list-style: none;

            &:hover{
                color: #069514;
            }
        }
    }

    &Btn{
        display: flex;
        align-items: center;
        justify-content: center;
        background: #069514;
        border-radius: 10px;
        color: #fff;
        text-decoration: none;
        font-size: 16px;
        max-width: 280px;
        border: none;
        outline: none;
        width: 100%;
    }
}

.history{
    &Item{
        margin-bottom: 10px;
    }

    &Top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 5px 20px;
        background: #413E3E;
        border-radius: 10px 10px 0px 0px;
    }

    &Name{
        color: #fff;
        font-size: 17px;
    }

    &Date{
        color: #A6A6A6;
        font-size: 15px;
    }

    &Body{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 20px;
        background: #1B1B1B;
        border-radius: 0px 0px 10px 10px;
    }

    &Text{
        color: #FFFFFF;
        font-size: 17px;
    }

    &Stars{
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &Title{
        margin-bottom: 20px;
        color: #A6A6A6;
        font-size: 16px;
    }
}

@media(max-width: 1000px){
    .content{
        flex-direction: column;
    }

    .chat{
        height: 400px;
    }

    .title{
        margin-bottom: 30px;
    }

    .inputs{
        flex-direction: column;

        input{
            width: 100%;
        }
    }

}

@media(max-width: 600px){
    .table{
        overflow-x: scroll;
        
        & > div{
            min-width: 600px;
        }
    }

    .buyContent{
        flex-direction: column;

        a{
            height: 50px;
            max-width: none;
        }
    }

    .historyBody{
        flex-direction: column;
        gap: 20px;
    }

    .selectList{
        top: 56px;
        width: 100%;
    }
}
